@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700;900&display=swap");

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Roboto", sans-serif;
}

body {
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  padding: 20px;
  background-color: #13005f;
  color: white;
  height: 100hv;
  width: 100hv;
}

.photo-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  /* grid-template-columns: repeat(auto-fit, minmax(200px, 1fr)); */
  grid-gap: 15px;
  margin-top: 1.2rem;
}

.photo {
  width: 200px;
  height: 200px;
  object-fit: cover;
  border-radius: 12px;
  cursor: pointer;
  border:1px solid #b393d3;
}

.photo:hover {
  transition: transform 0.5s;
  transform: scale(1.05);
  box-shadow: 0px 0px 5px #b393d3, 0px 0px 5px #b393d3, 0px 0px 5px #b393d3,
    0px 0px 5px #b393d3;
}

.buttons {
  display: flex;
}

.prev-button,
.next-button {
  background: #8050b0;
  border-radius: 50px;
  padding: 5px 10px;
  color: white;
  font-size: 18px;
  font-weight: 300;
  cursor: pointer;
  border: 4px solid #2c004773;
  justify-content: space-between;
  flex-direction: row;
}

.prev-button:disabled {
  opacity: 0.3;
}

.type-button {
  background: #8050b0;
  border-radius: 12px;
  padding: 5px 10px;
  color: white;
  font-size: 18px;
  font-weight: 300;
  cursor: pointer;
  border: 4px solid #2c004773;
  margin: auto;
}